import React, { FC, useState } from "react";

import LinkButton from "../../Button/LinkButton";
import { AlignPosition, SidePosition } from "../../DropDown";
import DropDown from "../../DropDown/DropDown";
import DropDownMenuItem from "../../DropDown/DropDownMenuItem";


interface RowsPerPageDropdownProps {
	align?: AlignPosition,
	ariaLabel: string,
	currentPage: number,
	dataQA: string,
	onChangeRowsPerPage: (currentRowsPerPage: number, currentPage: number) => void,
	rowOptions: Array<{ value: number, label: string }>
	side?: SidePosition,
	rowsPerPage: number,
}

const RowsPerPageDropdown: FC<RowsPerPageDropdownProps> = ({
	align = "start",
	ariaLabel,
	currentPage,
	dataQA,
	onChangeRowsPerPage,
	rowsPerPage,
	rowOptions,
	side = "bottom",
}) => {

	const [selectedValue, setSelectedValue] = useState(rowsPerPage);

	const handleSelect = (option: { value: number, label: string }) => {
		setSelectedValue(option.value);
		onChangeRowsPerPage(option.value, currentPage);
	};

	return (
		<DropDown
			dataQA={`${dataQA}-flyout`}
			ariaLabel={`${ariaLabel}-flyout`}
			side={side}
			align={align}
			sideOffset={1}
			$menuWidth="40px"
			$menuHeight="100px"
			trigger={(
				<LinkButton
					dataQA="dropdown button"
					buttonText={selectedValue}
					rightIcon="ChevronDown"
				/>
			)}
		>
			{rowOptions.map((option, index) => (
				<DropDownMenuItem
					key={option.label}
					onClick={() => handleSelect(option)}
					aria-current={selectedValue === index}
					$isSelected={index === selectedValue}
					tabIndex={0}
					role="listitem"
				>
					{option.label}
				</DropDownMenuItem>
			))}
		</DropDown>
	);
};

RowsPerPageDropdown.displayName = "RowsPerPageDropdown";
export default RowsPerPageDropdown;
